<template>
  <ndt-article :article-id="articleId" />
</template>

<script>
  import NdtArticle from '@/components/NdtArticle'

  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'Article',

    components: {
      NdtArticle
    },

    props: {
      slug: {
        type: String,
        default: null
      }
    },

    computed: {
      ...mapGetters({
        articles: 'articles/articles'
      }),

      articleId () {
        const article = this.articles
          .find(({ slug }) => slug === this.slug)

        return article ? article.id : null
      }
    },

    methods: {
      ...mapActions({
        setArticles: 'articles/setArticles'
      })
    },

    async mounted () {
      const articles = await this.$api.get('articles')

      this.setArticles(articles)
    }
  }
</script>
