<template>
  <article
    :class="{ 'NDT-Article--Link': excerpt }"
    class="NDT-Article"
    @click="onClick"
  >
    <h1 class="NDT-Article__Header">
      <template v-if="!excerpt">
        <span>
          {{ article.name }}
        </span>
      </template>
      <template v-else>
        <router-link :to="articleLink">
          {{ article.name }}
        </router-link>
      </template>
      <section class="NDT-Article__Meta">
        <div class="NDT-Article__Meta__Updated">
          <template v-if="article.updated === article.created">
            Publisert <strong>{{ publised }}</strong>
          </template>
          <template v-else>
            Sist oppdatert <strong>{{ updated }}</strong>
          </template>
        </div>
      </section>
    </h1>

    <template v-if="article.banner">
      <img
        :src="banner"
        :class="{ 'NDT-Article__Banner--Excerpt': excerpt }"
        class="NDT-Article__Banner"
      >
    </template>

    <div
      class="NDT-Article__Intro"
      :class="{ 'NDT-Article__Intro--Excerpt': excerpt }"
      v-html="article.intro"
    />

    <template v-if="!excerpt">
      <div class="NDT-Article__Body" v-html="article.body" />
    </template>
    <template v-else>
      <router-link
        :to="articleLink"
        class="NDT-Article__ReadMore"
      >
        Les mer <icon icon="arrow-right" />
      </router-link>
    </template>
  </article>
</template>

<script>
  import moment from 'moment'
  import { mapGetters } from 'vuex'
  import cdnImage from '@/utils/cdnImage'

  export default {
    name: 'NDTArticle',

    props: {
      articleId: {
        type: Number,
        required: true
      },

      excerpt: {
        type: Boolean,
        default: false
      }
    },

    computed: {
      ...mapGetters({
        articles: 'articles/articles'
      }),

      article () {
        return this.articles
        .find(({ id }) => id === this.articleId)
      },

      updated () {
        return moment(this.article.updated)
          .format('DD/MM/YYYY')
      },

      published () {
        return moment(this.article.published)
          .format('DD/MM/YYYY')
      },

      banner () {
        if (this.article.banner) {
          const crop = this.excerpt ? 'rc' : 'l'
          return cdnImage(this.article.banner, crop, 800, 400)
        }
      },

      articleLink () {
        return {
          name: 'article',
          params: {
            slug: this.article.slug
          }
        }
      }
    },

    methods: {
      onClick () {
        if (this.excerpt) {
          this.$router.push(this.articleLink)
        }
      }
    }
  }
</script>

